.add-scroll-active {
	overflow-y: auto;
	height: calc(96vh - 55px);
  }
  /* Thumb */
  ::-webkit-scrollbar-thumb {
	background: rgb(119, 145, 221);
	border-radius: 30px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
	background: transparent;
  }
  /* Hover */
  ::-webkit-scrollbar-thumb:hover {
	background: rgb(69, 93, 163);
  }

